.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 2rem 0;
}

.frame {
  border: 1px solid lightblue;
  margin: 1rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.frames {
  border: 1px solid gray;
  padding: 1rem;
  margin: 1rem;
}

.post-container {
  margin-top: 2rem;
}